<template>
    <div class="talion-nav-primary">
        <router-link :to="{name:'HomeView'}" class="logo"></router-link>
        <nav>
            <router-link :to="{name:'MovieView'}" style="color: #2384E8;">电影</router-link>
            <router-link :to="{name:'BookView'}" style="color: #9F7860;">图书</router-link>
            <router-link :to="{name:'StatusView'}" style="color: #E4A813;">广播</router-link>
            <router-link :to="{name:'GroupView'}" style="color: #2AB8CC;">小组</router-link>
            <span class="show-talion-nav" @click="showTalionNav"></span>
        </nav>
    </div>
</template>
<script>
export default {
    name: 'talion-nav-primary',
    methods: {
        showTalionNav(){
            this.$emit('showTalionNav')
        }
    }
}
</script>

<style lang="scss">
.talion-nav-primary {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    box-sizing: border-box;
    height: 4.6rem;
    border-bottom: 0.1rem solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.7rem;
    background-color: white;
    min-width: 32rem;

    nav {
        display: flex;
        align-items: center;
    }
    a {
        text-decoration: none;
        font-size: 1.7rem;
        margin-right: 1.7rem;
        line-height: 2.3rem;
    }
    .logo {
        width: 4.6rem;
        height: 2.2rem;
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
    }
    .show-talion-nav {
        background: url(data:image/png;base64,) left center no-repeat;
        width: 2.5rem;
        height: 2.3rem;
        background-size: contain;
    }
}
</style>



